.div-form-ajout {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 2px solid black;
  padding: 3%;
  padding-top: 0;
  border-radius: 25px;
  font-family: "Courier New", Courier, monospace;
}

.div-form-ajout .form-ajout {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #c4b8b8;
  padding: 3%;
  border-radius: 25px;
  font-family: "Courier New", Courier, monospace;
}

.div-form-ajout .form-ajout .label-form {
  display: flex;
  margin: 10px;
  align-items: center;
  font-weight: bold;
}

.div-form-ajout .form-ajout .label-form input {
  width: 100%;
  font-family: "Courier New", Courier, monospace;
  font-size: medium;
  margin-left: 5px;
  border-radius: 50px;
  padding: 15px;
}

.div-form-ajout .form-ajout button {
  width: fit-content;
  background-color: white;
  border-radius: 25px;
  padding: 3%;
  font-weight: bold;
  font-size: large;
  border: none;
  font-family: "Courier New", Courier, monospace;
  transform: scale(1.1);
  transition: 300ms transform ease;
}

.div-form-ajout .form-ajout button:hover {
  transform: scale(1);
  transition: 300ms transform ease;
}
